//colors
$body-background:             #fff;
$color-red:                   #F26565;
$color-yellow:                #F3D93E;
$color-black:                 #1F1F1F;

$transition-time:             0.4s;
//typography
$font-thin:                   100;
$font-light:                  300;
$font-regular:                400;
$font-medium:                 600;
$font-semibold:               $font-medium;
$font-bold:                   700;
$font-bolder:                 900;



$font-family-base:           'Avenir Next', sans-serif;
$font-weight-base:            $font-regular;
$font-size-base:              15px;
$font-color-base:             #000000;
$line-height-base:            1.4;
$links-color:                 $color-red;

//headings
$headings-font-family:        $font-family-base !default;
$headings-font-weight:        $font-semibold !default;
$headings-line-height:        1.1 !default;
$headings-color:              #000 !default;

$font-size-h1:                35px;
$font-size-h2:                30px;
$font-size-h3:                25px;
$font-size-h4:                20px;
$font-size-h5:                18px;
$font-size-h6:                17px;


//layout
$container-width:            1200px !default;
$popup-width:                480px!default;

//grid
$grid-columns:               12 !default;
$grid-gutter-width:          30px !default;

$screen-xxs:                 425px !default;
$screen-xs:                  550px !default;
$screen-sm:                  768px !default;
$screen-md:                  1024px !default;
$screen-container:           $container-width;//1200
$screen-lg:                  1366px !default;
$screen-hover:               $screen-sm !default; 

$screen-xs-min:              $screen-xs !default;
$screen-phone:               $screen-xs-min !default;
$screen-sm-min:              $screen-sm !default;
$screen-tablet:              $screen-sm-min !default;
$screen-md-min:              $screen-md !default;
$screen-desktop:             $screen-md-min !default;
$screen-lg-min:              $screen-lg !default;
$screen-lg-desktop:          $screen-lg-min !default;
$screen-xs-max:              ($screen-sm-min - 1) !default;
$screen-sm-max:              ($screen-md-min - 1) !default;
$screen-md-max:              ($screen-lg-min - 1) !default;





