@import "_components/component-files";

.c-header {
  position: relative;
  z-index: 300;

  #{&} {
    &__top {
      background: $color-black;
      padding: 27px 0;
      color: #ffffff;
      &-inner {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        @include mediaMax($screen-sm){
          display: block;
        }
      }
    }
    &__logo{
      display: flex;
      align-items: center;
      justify-content: flex-start;
      margin-right: 20px;
      flex: 1 1 auto;
      max-width: 300px;
      @include mediaMax($screen-container){
        display: block;
      }
      @include mediaMax($screen-xs){
        margin-right: 0;
      }
      img{
        max-height: 40px;
      }
      &-text{
        flex: 0 0 270px;
        max-width: 270px;
        font-size: 16px;
        line-height: 20px;
        font-weight: $font-medium;
        color: #DEDEDE;
        border-left: 1px solid #545454;
        padding-left: 20px;
        margin-left: 20px;
        @include mediaMax($screen-container){
          display: none;
        }
        p{
          margin: 0;
        }
      }
    }
    &__actions{
      flex: 0 0 auto;
      margin-left: auto;
      display: flex;
      align-items: flex-start;
      justify-content: flex-start;
      flex-direction: row-reverse;
      @include mediaMax($screen-sm){
        margin: 20px 0 0 0;
        flex-direction: row;
      }
      @include mediaMax($screen-xxs){
        display: block;
      }
    }
    &__form{
      @extend .c-btn;
      @extend .c-btn--yellow!optional;
      flex: 0 0 auto;
      text-transform: uppercase;
      @include mediaMax($screen-xxs){
        display: block;
        width: 100%;
      }
    }
    &__phone{
      @extend .c-btn;
      @extend .c-btn--red!optional;
      flex: 0 0 auto;
      position: relative;
      padding-left: 45px;
      white-space: nowrap;
      margin-left: 20px;
      [class^="c-icon"]{
        position: absolute;
        left: 15px;
        top: 50%;
        margin-top: -8px;
      }
      @include mediaMax($screen-sm){
        margin-left: 0;
        margin-right: 20px;
      }
      @include mediaMax($screen-xxs){
        margin-right: 0;
        margin-bottom: 10px;
        display: block;
        width: 100%;
      }
    }
  }
}
.c-footer {
  position: relative;
  z-index: 300;
  border-top: 1px solid $color-yellow;
  padding: 30px 0;

  #{&} {
    &__inner {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      flex-direction: row-reverse;
      @include mediaMax($screen-sm){
        display: block;
      }
    }
    &__top{
      margin-left: auto;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      @include mediaMax($screen-xs){
        display: block;
      }
    }
    &__bottom{
      flex: 1 1 auto;
      display: flex;
      align-items: stretch;
      justify-content: flex-start;
      flex-direction: row-reverse;
      margin-right: 20px;
      @include mediaMax($screen-md){
        display: block;
      }
      @include mediaMax($screen-sm){
        margin-right: 0;
        margin-top: 20px;
        display: flex;
        align-items: flex-start;
        justify-content: flex-start;
        flex-direction: row;
      }
      @include mediaMax($screen-xs){
        display: block;
        text-align: center;
        margin-top: 20px;
      }
    }

    &__payments{
      margin-left: 30px;
      @include mediaMax($screen-sm){
        margin-left: auto;
      }
      @include mediaMax($screen-xs){
        text-align: center;
      }
      ul{
        padding: 0;
        margin: 0 -5px;
        font-size: 0;
        line-height: 0;
        li{
          display: inline-block;
          vertical-align: top;
          padding: 0 5px;
          margin: 0;
        }
      }
    }
    &__social{
      display: flex;
      align-items: center;
      justify-content: flex-start;
      @include mediaMax($screen-xs){
        justify-content: center;
        margin-bottom: 20px;
        text-align: center;
      }
      @include mediaMax($screen-xxs){
        display: block;
      }

      &-text{
        font-size: 13px;
        line-height: 15px;
        font-weight: $font-regular;
        color: #000000;
        margin-left: 20px;
        padding-left: 20px;
        border-left: 1px solid #E0E0E0;
        @include mediaMax($screen-md){
          display: none;
        }
        p{
          margin: 0;
        }
      }
    }
    &__info{
      margin-left: 20px;
      padding-left: 20px;
      border-left: 1px solid #E0E0E0;
      @include mediaMax($screen-md){
        padding-left: 0;
        border-left: none;
        margin-left: 0;
      }
      @include mediaMax($screen-sm){
        margin-right: auto;
      }
      @include mediaMax($screen-xs){
        text-align: center;
        margin-bottom: 10px;
      }
    }
    &__copyright{
      @include mediaMax($screen-sm){
        text-align: right;
      }
      @include mediaMax($screen-xs){
        text-align: center;
      }
    }
    &__info,
    &__copyright{
      font-size: 13px;
      line-height: 15px;
      font-weight: $font-regular;
      color: #000000;
      p{
        margin: 0;
      }
    }
    &__license{
      a{
        text-decoration: none;
      }
    }
  }
}
.c-promo{
  display: block;
  position: relative;
  min-height: 200px;
  border-radius: 10px;
  overflow: hidden;
  color: #ffffff;
  font-size: 22px;
  line-height: 25px;
  padding: 60px 400px 60px 60px;
  background-color: #01083a;
  background-image:
          linear-gradient(135deg, rgba(4,7,44,0) 0%, rgba(48,28,57,0) 80%, rgba(59,33,60,1) 100%),
          linear-gradient(to right, rgba(1,8,58,1) 0%, rgba(13,10,37,1) 100%);
  background-position: 0 0,0 0;
  background-repeat: no-repeat;
  @include mediaMax($screen-md){
    padding: 60px;
    text-align: center;
  }
  @include mediaMax($screen-xs){
    font-size: 18px;
    line-height: 20px;
    padding: 40px 30px;
  }
  b,strong{
    font-weight: $font-semibold;
  }
  p{
    margin: 0;
  }
  #{&}{
    &__title{
      font-size: 35px;
      line-height: 40px;
      font-weight: $font-bold;
      margin: 0 0 10px 0;
      @include mediaMax($screen-xs){
        font-size: 20px;
        line-height: 25px;
      }
    }
    &__img{
      font-size: 0;
      line-height: 0;
      position: absolute;
      right: 60px;
      top: 30px;
      width: 400px;
      @include mediaMax($screen-md){
        position: relative;
        right: auto;
        top: auto;
        width: auto;
        text-align: center;
        margin-bottom: 30px;
      }
    }
  }
}
.c-logo {
  font-size: 0;
  line-height: 0;

  a {
    text-decoration: none;
  }

  img {
    max-width: 100%;
  }

}
.c-item-category{
  text-align: center;
  margin-bottom: 30px;
  #{&}{
    &__img{
      display: block;
      text-align: center;
      font-size: 0;
      line-height: 155px;
      height: 155px;
      margin-bottom: 15px;
      img{
        display: inline-block;
        vertical-align: bottom;
        width: auto;
        max-height: 155px;
      }
    }
    &__title{
      display: block;
      text-decoration: none;
      font-weight: $font-regular;
      font-size: 18px;
      line-height: 20px;
      color: $color-black;
      margin: 0;
    }
  }
}
.c-item-brand{
  display: block;
  height: 100px;
  line-height: 100px;
  text-align: center;
  font-size: 0;
  img{
    display: inline-block;
    vertical-align: middle;
    width: auto;
    max-height: 100px;
  }
}
.c-item-product{
  text-align: center;
  #{&}{
    &__img{
      display: block;
      text-align: center;
      font-size: 0;
      line-height: 100px;
      height: 100px;
      margin-bottom: 10px;
      img{
        display: inline-block;
        vertical-align: bottom;
        width: auto;
        max-height: 155px;
      }
    }
    &__title{
      display: block;
      text-decoration: none;
      font-weight: $font-regular;
      font-size: 18px;
      line-height: 20px;
      color: $color-black;
      margin: 0;
    }
  }
}


//sections
.c-section {
  position: relative;
  display: block;

  #{&} {
    &__head {
      text-align: center;
      margin-bottom: 30px;
      font-size: 25px;
      @include mediaMax($screen-xs){
        font-size: 18px;
        margin-bottom: 20px;
      }
    }
    &__title {
      font-size: 45px;
      font-weight: $font-regular;
      margin: 0 0 15px 0;
      @include mediaMax($screen-xs){
        font-size: 30px;
        font-weight: $font-semibold;
      }
    }

  }
}
.c-section-promo{
  min-height: 630px;
  padding: 115px 0 250px 0;
  background: #545454 url("../images/bg-promo.jpg") top center no-repeat;
  background-size: cover;
  color: #ffffff;
  z-index: 20;
  overflow: hidden;
  margin-bottom: -220px;
  @include mediaMax($screen-md){
    padding: 100px 0;
    margin-bottom: 0;
    min-height: auto;
  }
  @include mediaMax($screen-xs){
    padding: 40px 0;
  }
  .c-section__head{
    strong,
    b{
      color: $color-red;
    }
  }
  .c-section__title{
    color: #ffffff;
    font-weight: $font-bold;
  }
  &:before,&:after{
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1;
  }
  &:before{
    background: rgba(#545454, 0.8);
    left: 0;
    top: 0;
  }
  &:after{
    top: 100%;
    left: 0;
    background: #ffffff;
    transform: skewY(-4deg);
    transform-origin: left top;
  }
  #{&}{
    &__inner{
      text-align: center;
      max-width: 800px;
      margin: 0 auto;
    }
  }
}
.c-section-features{
  z-index: 25;
  margin-bottom: 125px;
  @include mediaMax($screen-xs){
    margin-bottom: 50px;
  }
  #{&}{
    &__inner{
      display: flex;
      align-items: flex-start;
      justify-content: flex-start;
      @include mediaMax($screen-md){
        display: block;
      }
    }
    &__features{
      flex: 1 1 auto;
      padding-top: 250px;
      margin-top: auto;
      @include mediaMax($screen-md){
        padding: 40px 0;
      }
    }
    &__item{
      display: block;
      font-size: 20px;
      font-weight: $font-bold;
      color: #000000;
      margin-bottom: 30px;
      @include mediaMax($screen-xs){
        font-size: 18px;
        margin-bottom: 20px;
      }
      p{
        margin: 0;
      }

      &-number{
        font-size: 55px;
        line-height: 1;
        margin: 0 0 10px 0;
        color: $color-red;
        font-weight: $font-semibold;
        @include mediaMax($screen-xs){
          font-size: 35px;
        }
      }
    }
    &__form{
      flex: 0 0 570px;
      max-width: 570px;
      margin-left: 30px;
      background: $color-yellow;
      border-radius: 10px;
      padding: 35px 70px 40px 70px;
      @include mediaMax($screen-md){
        max-width: none;
        margin-left: 0;
      }
      @include mediaMax($screen-xs){
        padding: 30px;
      }
    }
  }
}
.c-section-faq{
  z-index: 20;
  margin-bottom: 100px;
  @include mediaMax($screen-xs){
    margin-bottom: 50px;
  }
  &:before{
    z-index: -1;
    content: '';
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    top: 50px;
    bottom: 150px;
    background: #f3cd59;
    background: linear-gradient(to right, rgba(243,217,70,1) 0%, rgba(237,174,39,1) 100%);
    transform: skewY(-3deg);
    transform-origin: right top;
  }
  #{&}{
    &__inner{}
    &__banner{
      margin-bottom: 100px;
      @include mediaMax($screen-xs){
        margin-bottom: 40px;
      }
    }
    &__list{
      .l-row{
        align-items: stretch;
        [class^="l-col"]{
          display: flex;
          align-items: stretch;
          justify-content: flex-start;
        }
      }
    }
    &__item{
      width: 100%;
      display: flex;
      align-items: stretch;
      justify-content: flex-start;
      flex-direction: column;
      padding: 50px 40px;
      border-radius: 10px;
      background: $color-black;
      color: #ffffff;
      font-size: 18px;
      line-height: 1.4;
      font-weight: $font-regular;
      text-align: center;
      min-height: 415px;
      @include mediaMax($screen-xs){
        min-height: auto;
        display: block;
        padding: 30px;
      }
      p{
        margin: 0;
      }
      &-body{
        flex: 1 1 auto;
      }
      &-footer{
        margin-top: 40px;
        @include mediaMax($screen-xs){
          margin-top: 30px;
        }
      }
      &-title{
        font-size: 30px;
        font-weight: $font-semibold;
        margin: 0 0 15px 0;
        @include mediaMax($screen-xs){
          font-size: 25px;
          margin: 0 0 10px 0;
        }
      }
      &-btn{
        text-align: center;
      }
      &-phone{
        font-size: 30px;
        font-weight: $font-bold;
        line-height: 1;
        padding: 10px 0;
        margin: 15px 0;
        border-top: 1px solid #484848;
        border-bottom: 1px solid #484848;
        @include mediaMax($screen-xs){
          font-size: 25px;
        }
        a{
          color: currentColor;
          text-decoration: none;
        }
      }

    }
  }
}
.c-section-catalog{
  margin-bottom: 70px;
  @include mediaMax($screen-xs){
    margin-bottom: 50px;
  }
  #{&}{
    &__list{}
    &__brands{
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      flex-wrap: wrap;
      margin: 0 -10px;
      @include mediaMax($screen-container){
        justify-content: flex-start;
      }
      &-item{
        padding: 0 10px;
        flex: 0 0 150px;
        max-width: 150px;
        @include mediaMax($screen-container){
          flex: 0 0 20%;
          max-width: 20%;
        }
        @include mediaMax($screen-sm){
          flex: 0 0 33.33%;
          max-width: 33.33%;
        }
        @include mediaMax($screen-xs){
          flex: 0 0 50%;
          max-width: 50%;
        }
      }
      &.owl-carousel{
        display: block;
        margin: 0;
        .owl-item{
          .c-section-catalog__brands-item{
            display: block;
            max-width: none;
            padding: 0;
            img{
              width: auto;
              display: inline-block;
            }

          }
        }
      }
    }
    &__products{
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      flex-wrap: wrap;
      margin: 0 -10px;
      &:after{
        content: '';
        display: block;
        flex: 1 1 auto;
      }
      &-item{
        flex: 0 0 150px;
        max-width: 150px;
        padding: 0 10px;
        margin-bottom: 20px;
        @include mediaMax($screen-xs){
          flex: 0 0 50%;
          max-width: 50%;
        }
      }
    }
    &__more{
      font-weight: $font-regular;
      font-size: 18px;
      line-height: 20px;
      color: $color-black;
      &-num{
        font-size: 55px;
        font-weight: $font-semibold;
        color: $color-red;
        height: 100px;
        line-height: 100px;
        margin-bottom: 15px;
      }
      p{
        margin: 0;
      }
    }
  }
}
.c-section-feedback{
  z-index: 20;
  position: relative;
  color: #ffffff;
  padding-top: 150px;
  padding-bottom: 300px;
  margin-bottom: -220px;
  @include mediaMax($screen-xs){
    padding: 50px 0 100px 0;
    margin-bottom: -50px;
  }
  &:before{
    z-index: -1;
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #01083a;
    background-image:
            linear-gradient(135deg, rgba(4,7,44,0) 0%, rgba(48,28,57,0) 80%, rgba(59,33,60,1) 100%),
            linear-gradient(to right, rgba(1,8,58,1) 0%, rgba(13,10,37,1) 100%);
    transform: skewY(-3deg);
    transform-origin: right top;
  }
  .c-section__head{
    color: #ffffff;
  }
  .c-section__title{
    font-weight: $font-regular;
    color: #ffffff;
  }
  #{&}{
    &__form{
      @include mediaMax($screen-md){
        max-width: 400px;
        margin: 0 auto;
      }


    }
  }
}
.c-section-about{
  z-index: 25;
  margin-bottom: 30px;
  .c-section__head{
    text-align: left;
    margin: 0 0 10px 0;
  }
  .c-section__title{
    font-weight: $font-semibold;
    font-size: 35px;
  }
  #{&}{
    &__map{
      height: 470px;
      border-radius: 10px;
      overflow: hidden;
      position: relative;
      margin-bottom: 30px;
      @include mediaMax($screen-xs){
        height: 300px;
      }
      &-box{
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
      }
    }
  }
}