body{
  &:before{
    content: 'xxs';
    display: none;
  }
  @include mediaMin($screen-xxs){
    &:before{
      content: 'xs';
    }
  }
  @include mediaMin($screen-xs){
    &:before{
      content: 'sm';
    }
  }
  @include mediaMin($screen-sm){
    &:before{
      content: 'md';
    }
  }
  @include mediaMin($screen-md){
    &:before{
      content: 'container';
    }
  }
  @include mediaMin($screen-container){
    &:before{
      content: 'lg';
    }
  }
}
