.h-clear {
 @include clearfix;
}

.h-pull-right {
  float: right;
}
.h-pull-left {
  float: left;
}

.h-no-margin{
  margin: 0!important;;
}

.h-rounded{
  border-radius: 50%;
}

.h-no-border {
  border: none!important;;
}

.h-justify{
  width: 100%;
}

.h-text-center{
  text-align: center;
}
.h-text-muted{
  color: #ccc;
}

.h-font-inherit{
  @include fontInherit;
}
.h-font-light{
  font-weight: $font-light;
}
.h-font-regular{
  font-weight: $font-regular;
}
.h-font-medium,
.h-font-semibold{
  font-weight: $font-semibold;
}
.h-font-bold{
  font-weight: $font-bold;
}
.h-vertical-middle{
  vertical-align: middle!important;
}
.h-vertical-top{
  vertical-align: top!important;
}
.h-vertical-bottom{
  vertical-align: top!important;
}
.h-hidden{
    display: none!important;
}
.h-hidden-xs-up{
  @include mediaMin($screen-xs){
    display: none!important;
  }
}
.h-hidden-xs-down{
  @include mediaMax($screen-xs){
    display: none!important;
  }
}
.h-hidden-sm-up{
  @include mediaMin($screen-sm){
    display: none!important;
  }
}
.h-hidden-sm-down{
  @include mediaMax($screen-sm){
    display: none!important;
  }
}
.h-hidden-md-up{
  @include mediaMin($screen-md){
    display: none!important;
  }
}
.h-hidden-md-down{
  @include mediaMax($screen-md){
    display: none!important;
  }
}
.h-hidden-lg-up{
  @include mediaMin($screen-lg){
    display: none!important;
  }
}
.h-hidden-lg-down{
  @include mediaMax($screen-lg){
    display: none!important;
  }
}
.h-hidden-container-up{
  @include mediaMin($screen-container){
    display: none!important;
  }
}
.h-hidden-container-down{
  @include mediaMax($screen-container){
    display: none!important;
  }
}

.h-flex-row            { flex-direction: row !important; }
.h-flex-column         { flex-direction: column !important; }
.h-flex-row-reverse    { flex-direction: row-reverse !important; }
.h-flex-column-reverse { flex-direction: column-reverse !important; }

.h-justify-content-start   { justify-content: flex-start !important; }
.h-justify-content-end     { justify-content: flex-end !important; }
.h-justify-content-center  { justify-content: center !important; }
.h-justify-content-between { justify-content: space-between !important; }
.h-justify-content-around  { justify-content: space-around !important; }

.h-align-items-start    { align-items: flex-start !important; }
.h-align-items-end      { align-items: flex-end !important; }
.h-align-items-center   { align-items: center !important; }
.h-align-items-baseline { align-items: baseline !important; }
.h-align-items-stretch  { align-items: stretch !important; }

.h-align-content-start   { align-content: flex-start !important; }
.h-align-content-end     { align-content: flex-end !important; }
.h-align-content-center  { align-content: center !important; }
.h-align-content-between { align-content: space-between !important; }
.h-align-content-around  { align-content: space-around !important; }
.h-align-content-stretch { align-content: stretch !important; }

.h-align-self-auto     { align-self: auto !important; }
.h-align-self-start    { align-self: flex-start !important; }
.h-align-self-end      { align-self: flex-end !important; }
.h-align-self-center   { align-self: center !important; }
.h-align-self-baseline { align-self: baseline !important; }
.h-align-self-stretch  { align-self: stretch !important; }


@mixin flex-media-helpers($screen,$suffix) {
  @media (min-width: $screen) {
    .h-flex-#{$suffix}-row            { flex-direction: row !important; }
    .h-flex-#{$suffix}-column         { flex-direction: column !important; }
    .h-flex-#{$suffix}-row-reverse    { flex-direction: row-reverse !important; }
    .h-flex-#{$suffix}-column-reverse { flex-direction: column-reverse !important; }

    .h-justify-content-#{$suffix}-start   { justify-content: flex-start !important; }
    .h-justify-content-#{$suffix}-end     { justify-content: flex-end !important; }
    .h-justify-content-#{$suffix}-center  { justify-content: center !important; }
    .h-justify-content-#{$suffix}-between { justify-content: space-between !important; }
    .h-justify-content-#{$suffix}-around  { justify-content: space-around !important; }

    .h-align-items-#{$suffix}-start    { align-items: flex-start !important; }
    .h-align-items-#{$suffix}-end      { align-items: flex-end !important; }
    .h-align-items-#{$suffix}-center   { align-items: center !important; }
    .h-align-items-#{$suffix}-baseline { align-items: baseline !important; }
    .h-align-items-#{$suffix}-stretch  { align-items: stretch !important; }

    .h-align-content-#{$suffix}-start   { align-content: flex-start !important; }
    .h-align-content-#{$suffix}-end     { align-content: flex-end !important; }
    .h-align-content-#{$suffix}-center  { align-content: center !important; }
    .h-align-content-#{$suffix}-between { align-content: space-between !important; }
    .h-align-content-#{$suffix}-around  { align-content: space-around !important; }
    .h-align-content-#{$suffix}-stretch { align-content: stretch !important; }

    .h-align-self-#{$suffix}-auto     { align-self: auto !important; }
    .h-align-self-#{$suffix}-start    { align-self: flex-start !important; }
    .h-align-self-#{$suffix}-end      { align-self: flex-end !important; }
    .h-align-self-#{$suffix}-center   { align-self: center !important; }
    .h-align-self-#{$suffix}-baseline { align-self: baseline !important; }
    .h-align-self-#{$suffix}-stretch  { align-self: stretch !important; }
  }
}

@include flex-media-helpers($screen-xs,xs);
@include flex-media-helpers($screen-sm,sm);
@include flex-media-helpers($screen-md,md);
@include flex-media-helpers($screen-lg,lg);
@include flex-media-helpers($screen-container,container);
