.c-btn{
  display: inline-block;
  vertical-align: top;
  cursor: pointer;
  border: none;
  width: auto;
  height: auto;
  text-align: center;
  text-transform: none;
  text-decoration: none;
  box-sizing: border-box;
  background: none;
  font-family: $font-family-base;
  margin: 0;
  outline: none;
  overflow: hidden;
  backface-visibility: hidden;
  transform: translate3d(0, 0, 0);
  background: $color-black;
  color: #ffffff;
  font-size: 16px;
  line-height: 20px;
  font-weight: $font-bold;
  padding: 13px 15px;
  border-radius: 4px;

  @include user-select();
  @include appearance();
  @include transition();
  @include hoverMedia(){
    opacity: 0.8;
  }

  &#{&}{
    &--yellow{
      color: $color-black;
      background: $color-yellow;
    }
    &--red{
      background: $color-red;
      color: #ffffff;
    }
    &--white{
      background: #FFFFFF;
      color: $color-black;
    }
    &--lg{
      @include mediaMin($screen-xs){
        padding: 19px 25px;
        font-size: 20px;
        line-height: 22px;
        font-weight: $font-semibold;
      }
    }
    &--block{
      display: block;
      width: 100%;
      box-sizing: border-box;
    }
  }
  &.is-loading{
    cursor: progress;
    &:after{
      content: '';
      display: block;
      position: absolute;
      background: url("../images/loader.svg") center center no-repeat;
      background-size: 20px 20px;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
    }
  }
}

.c-btn-scrolltop{
  position: fixed;
  font-size: 0;
  line-height: 0;
  cursor: pointer;
  opacity: 0;
  @include transition();
  &.is-active{
    opacity: 1;
  }
}
