[disabled],
.is-disabled{
  cursor: not-allowed;
}
.is-loading{
  cursor: progress;
}

textarea{
  display: block;
  width: 100%;
  resize: none;
  font-weight: $font-regular;
  font-size: 18px;
  line-height: 20px;
  color: #000000;
  background: #ffffff;
  border-radius: 4px;
  border: 1px solid #939393;
  padding: 19px;
  min-height: 100px;
  max-height: 300px;

  @include appearance;
  @include transition();

  @include placeholder(){
    color: #282828;
    @include transition();
  }

  &:hover,&:focus,&.is-changed{}

}
input[type="radio"],input[type="checkbox"]{
  border: none;
  box-shadow: none;
  background: none;
}
input[type="text"], input[type="email"],
input[type="datetime"],input[type="datetime-local"],
input[type="password"], input[type="tel"],
input[type="phone"], input[type="number"],
input[type="search"] {
  display: block;
  text-align: left;
  width: 100%;
  box-shadow: none;
  font-weight: $font-regular;
  font-size: 18px;
  line-height: 20px;
  color: #000000;
  background: #ffffff;
  border-radius: 4px;
  border: 1px solid #939393;
  padding: 19px;
  height: 60px;
  @include mediaMax($screen-xs){
    font-size: 14px;
    line-height: 18px;
    height: 40px;
    padding: 10px 20px;
  }

  @include appearance;
  @include transition();
  
  @include placeholder(){
    color: #282828;
    @include transition();
  }
  
  &:hover,&:focus, &.is-changed{}
}

.c-checkbox{
  display: inline-block;
  position: relative;
  cursor: pointer;
  text-align: left;
  color: currentColor;
  font-size: 15px;
  line-height: 18px;
  font-weight: $font-medium;
  padding-left: 35px;
  a{
    text-decoration: none;
  }
  #{&}{
    &__label{
      display: block;
    }
    &__icon{
      position: absolute;
      left: 0;
      top: 0;
      width: 18px;
      height: 18px;
      background: #ffffff;
      border-radius: 2px;
      border: 1px solid #F5D1D1;
      font-size: 12px;
      line-height: 16px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: $color-red;
      [class^="c-icon"]{
        vertical-align: middle;
        opacity: 0;
        transform: scale(0.5) rotate(-45deg);
        @include transition();
      }
    }
  }
  input{
    cursor: pointer;
    position: absolute;
    left: 0;
    top: 0;
    width: 18px;
    height: 18px;
    opacity: 0;
    background: none;

    &:disabled{
      cursor: not-allowed;
    }

    &:checked ~ .c-checkbox__icon{
      [class^="c-icon"]{
        opacity: 1;
        transform: scale(1) rotate(0);
      }
    }
    &:disabled{
      & ~ .c-checkbox__label,
      & ~ .c-checkbox__icon{
        opacity: 0.5;
        cursor: not-allowed;
      }
    }
  }
}
.c-field-addon{
  position: relative;
  &.is-active{
    .c-field-addon__icon{
      color: $color-red;
    }
  }
  .c-field-addon__icon{
    position: absolute;
    left: 0;
    top: 0;
    width: 50px;
    height: 100%;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;
    font-size: 24px;
    color: #4F4F4F;
    @include mediaMax($screen-xs){
      font-size: 16px;
      width: 40px;
    }
    img{
      max-height: 25px;
      width: auto;
    }
    [class^="c-icon"]{
      display: block;
      @include transition();
    }
  }
  textarea{
    & ~ .c-field-addon__icon{
      height: 50px;
    }
  }
  input[type="text"], input[type="email"],input[type="datetime-local"],
  input[type="password"], input[type="tel"],input[type="phone"],
  input[type="number"], input[type="search"],
  textarea,select,.ui-selectmenu-button{
    padding-left: 55px;
    @include mediaMax($screen-xs){
      padding-left: 40px;
    }
    &:hover,
    &:focus{
      & ~ .c-field-addon__icon{
        color: $color-red;
      }
    }
  }
}

  //forms
.c-form{
  position: relative;
  text-align: left;
  display: block;
  .c-form__title{
    font-size: 30px;
    font-weight: $font-semibold;
    margin: 0 0 20px 0;
    @include mediaMax($screen-xs){
      font-size: 20px;
      margin: 0 0 15px 0;
    }
  }
  .c-form__head{
    margin-bottom: 25px;
    text-align: center;
    @include mediaMax($screen-xs){
      margin-bottom: 20px;
    }
  }
  .c-form__label{}
  .c-form__field{
    position: relative;
    margin-bottom: 10px;
  }

  .c-form__footer{
    text-align: center;
    margin-top: 20px;
  }
  .c-form__license{
    margin-bottom: 20px;
  }
  .c-form__error{
    margin: 5px 0;
    color: #f1000d;
    p{
      margin: 0;
    }
    ul{
      padding: 0;
      margin: 0;
      display: block;
      li{
        margin: 0;
        padding:0 0 0 20px;
        position: relative;
        line-height: 1.2;
        display: block;
        color: currentColor;
        &:before,&:after{
          content: '';
          display: block;
          box-sizing: border-box;
          position: absolute;
          width: 8px;
          height: 2px;
          border-top: 2px solid;
          margin-top: -1px;
          left: 0;
          top: .6em;
          transform-origin: center;
        }
        &:before{
          transform: rotate(45deg);
        }
        &:after{
          transform: rotate(-45deg);
        }
      }
    }
  }
  .c-form__success{
    display: block;
    text-align: center;
    margin: 20px auto;
    p{
      margin: 0;
    }
    &-icon {
      font-size: 0;
      line-height: 0;
      display: block;
      text-align: center;
      margin-bottom: 10px;
    }
    &-title{
      @extend .c-form__title!optional;   
      margin: 0;
      color: currentColor;
    }
  }
}
.c-form-order{
  .c-form__body{
    @include mediaMin($screen-md){
      max-width: 350px;
      margin: 0 auto;
    }
  }
  .c-form__submit{
    max-width: 350px;
    margin: 0 auto;
  }
}
.c-form-feedback{
  .c-form__license{
    a{
      color: #F3D93E;
    }
  }
}