.c-icon {
  display: inline-block;
  line-height: 0;
  font-size: 0;
  padding: 0;
}
.c-icon-svg{
  color:inherit;
  fill: currentColor;
  stroke: currentColor;
  width: 1em;
  height: 1em;
  font-size: 1em;
}

@import "icons-img";
@import "icons-svg";

@mixin imgSprite($sprites) {
  @each $sprite in $sprites {
    $sprite-name: nth($sprite, 10);
    .c-icon-#{$sprite-name} {
      @include sprite($sprite);
    }
  }
}

@include imgSprite($spritesheet-sprites);

.c-icon-svg-call,
.c-icon-svg-user,
.c-icon-svg-wheel,
.c-icon-svg-flash,
.c-icon-svg-facebook,
.c-icon-svg-flikr,
.c-icon-svg-google,
.c-icon-svg-instagram,
.c-icon-svg-whatsapp,
.c-icon-svg-vkontakte,
.c-icon-svg-youtube,
.c-icon-svg-youtube2,
.c-icon-svg-linkedin,
.c-icon-svg-mailru,
.c-icon-svg-odnoklasniki,
.c-icon-svg-skype,
.c-icon-svg-viber,
.c-icon-svg-pinterest,
.c-icon-svg-telegram,
.c-icon-svg-twitter,
.c-icon-svg-tumblr{
  stroke: none;
}