html, body{
  font-family: $font-family-base;
  font-size: $font-size-base;
  color: $font-color-base;
  font-weight: $font-weight-base;
  line-height: $line-height-base;
  background: $body-background;
}

h1, h2, h3,h4,h5,h6,
.h1, .h2, .h3, .h4, .h5, .h6{
  font-family: $headings-font-family;
  line-height: $headings-line-height;
  color: $headings-color;
  font-weight: $headings-font-weight;
}

h1, .h1 { 
  font-size: $font-size-h1;
  margin: 0 0 25px 0;
}
h2, .h2 { 
  font-size: $font-size-h2;
  margin: 0 0 25px 0;
}
h3, .h3 { 
  font-size: $font-size-h3;
  margin: 0 0 15px 0;
}
h4, .h4 { 
  font-size: $font-size-h4; 
}
h5, .h5 { 
  font-size: $font-size-h5; 
}
h6, .h6 { 
  font-size: $font-size-h6; 
}
h4,h5,h6,.h4, .h5, .h6 {
  margin: 0 0 15px 0;
}
h1, .h1,
h2, .h2,
h3, .h3 {
  small,
  .small {
    font-size: 65%;
  }
}
h4, .h4,
h5, .h5,
h6, .h6 {
  small,
  .small {
    font-size: 75%;
  }
}
small,
.small {
  color: inherit;
  font-size: 75%;
}

* {
  outline: none;
  box-sizing:border-box;
}

img {
  border: none;
  max-width: 100%;
}

a{
  color: $links-color;
  text-decoration: underline;
  @include transition($type:linear);
  &:hover{
    text-decoration: none;
  }
}

p{
  margin: 0 0 10px 0;
}

ul, ol {
   margin: 15px 0;
}
ul{
  padding-left: 20px;
}
ol{
  padding-left: 20px;
}

table {
  border: 1px solid;
  padding: 0;
  margin: 15px 0;
  border-collapse: collapse;

  td,th {
    border: 1px solid;
  }
  th {
    text-align: center;
  }
}
