@mixin mediaMax ($screen){
  @media only screen and (max-width: $screen - 1){
    @content;
  }
}
@mixin mediaMin ($screen){
  @media only screen and (min-width: $screen){
    @content;
  }
}
@mixin mediaMinMax ($min, $max){
  @media only screen and (min-width: $min) and  (max-width: $max - 1){
    @content;
  }
}
@mixin hoverMedia($screen : $screen-hover){
  @include mediaMin($screen){
    &:hover{
      @content
    }
  }
}

