.l-container {
  width: 100%;
  max-width: $container-width;
  margin: 0 auto;
  box-sizing: border-box;
  padding: 0 15px;
  @include clearfix;
}

.l-main-wrapper{
  overflow: hidden;
  min-width: 320px;
  min-height: 100vh;
  display: flex;
  align-items: stretch;
  justify-content: flex-start;
  flex-direction: column;
}
.l-main-header{
  flex: 0 0 auto;
}
.l-main-body{
  position: relative;
  flex: 1 1 auto;
}
.l-main-footer{
  flex: 0 0 auto;
  margin-top: auto;
}

.l-main-body{
  position: relative;
}
.l-page-header{

  h1,.h1{
    margin: 0;
  }

}
.l-page-body{}

.l-page-content{
  @include clearfix;
  position: relative;
}

