@keyframes spinLeft {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes spinRight {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(-360deg);
  }
}
.a-spinLeft {
  animation-name: spinLeft;
  transform-origin: center center;
  animation-timing-function: linear;
}
.a-spinRight {
  animation-name: spinRight;
  transform-origin: center center;
  animation-timing-function: linear;
}
.a-infinite{
  animation-iteration-count: infinite;
}