.c-social-links{
    ul{
        padding: 0;
        margin: 0 -4px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 0;
        line-height: 0;
        li{
            padding: 0 4px;
            display:inline-block;
            vertical-align: top;
            a{
                text-decoration: none;
                text-align: center;
                line-height: 0;
                background: #E7E7E7;
                width: 28px;
                height: 28px;
                border-radius: 50%;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 14px;
                color: #000000;
                transition: all 0.2s;
                &.ok{
                    background: #F68634;
                    color: #ffffff;
                }
                &.fb{
                    background: #3B5998;
                    color: #ffffff;
                }
                &.vk{
                    background: #4C6C91;
                    color: #ffffff;
                }

                @include hoverMedia(){
                    transform: translate(0,-5px);
                }
            }
        }
    }
}
