.b-animation-gears{
  margin-bottom: 20px;
  color: #CCCCCC;
  [class^="c-icon"]{
    stroke-width: 0.3px;
  }
  #{&}{
    &__gear{
      font-size: 100px;
      animation-duration: 6000ms;
    }
    &__gear-sm{
      font-size: 50px;
      margin-bottom: 50px;
      animation-duration: 4000ms;
    }
  }
}