// Framework grid generation
//
// Used only by Bootstrap to generate the correct number of grid classes given
// any value of `$grid-columns`.

// [converter] This is defined recursively in LESS, but Sass supports real loops
@mixin make-grid-columns($i: 1, $list: ".l-col-xs-#{$i}, .l-col-sm-#{$i}, .l-col-md-#{$i}, .l-col-lg-#{$i}, .l-col-container-#{$i}, .l-col-#{$i}") {
  @for $i from (1 + 1) through $grid-columns {
    $list: "#{$list}, .l-col-xs-#{$i}, .l-col-sm-#{$i}, .l-col-md-#{$i}, .l-col-lg-#{$i}, .l-col-container-#{$i}, .l-col-#{$i}";
  }
  #{$list} {
    position: relative;
    min-height: 1px;
    padding-left:  ceil(($grid-gutter-width / 2));
    padding-right: floor(($grid-gutter-width / 2));
    box-sizing: border-box;
    flex: 1 0 auto;
    width: 100%;
  }
}


// [converter] This is defined recursively in LESS, but Sass supports real loops
@mixin float-grid-columns($class, $i: 1, $list: ".l-col-#{$class}-#{$i}") {
  @for $i from (1 + 1) through $grid-columns {
    $list: "#{$list}, .l-col-#{$class}-#{$i}";
  }
  #{$list} {

  }
}


@mixin calc-grid-column($index, $class, $type) {
  @if ($type == width) and ($index > 0) {
    .l-col-#{$class}-#{$index} {
      flex: 0 0 percentage(($index / $grid-columns));
      max-width: percentage(($index / $grid-columns));
    }
  }
  @if ($type == auto) and ($index > 0) {
    .l-col-#{$class}-auto {
      flex: 0 0 auto;
      max-width: 100%;
      width: auto;
    }
  }
  @if ($type == push) and ($index > 0) {
    .l-col-#{$class}-push-#{$index} {
      left: percentage(($index / $grid-columns));
    }
  }
  @if ($type == push) and ($index == 0) {
    .l-col-#{$class}-push-0 {
      left: auto;
    }
  }
  @if ($type == pull) and ($index > 0) {
    .l-col-#{$class}-pull-#{$index} {
      right: percentage(($index / $grid-columns));
    }
  }
  @if ($type == pull) and ($index == 0) {
    .l-col-#{$class}-pull-0 {
      right: auto;
    }
  }
  @if ($type == offset) {
    .l-col-#{$class}-offset-#{$index} {
      margin-left: percentage(($index / $grid-columns));
    }
  }
}

// [converter] This is defined recursively in LESS, but Sass supports real loops
@mixin loop-grid-columns($columns, $class, $type) {
  @for $i from 0 through $columns {
    @include calc-grid-column($i, $class, $type);
  }
}


// Create grid for specific class
@mixin make-grid($class) {
  @include float-grid-columns($class);
  @include loop-grid-columns(1, $class, auto);
  @include loop-grid-columns($grid-columns, $class, width);
  @include loop-grid-columns($grid-columns, $class, pull);
  @include loop-grid-columns($grid-columns, $class, push);
  @include loop-grid-columns($grid-columns, $class, offset);
}



// Generate the col-$size columns
@mixin make-column($columns, $gutter: $grid-gutter-width) {
  position: relative;
  min-height: 1px;
  padding-left:  ($gutter / 2);
  padding-right: ($gutter / 2);
  box-sizing: border-box;
  flex: 0 0 percentage(($columns / $grid-columns));
  max-width: percentage(($columns / $grid-columns));
}
@mixin make-column-offset($columns) {
  margin-left: percentage(($columns / $grid-columns));
}
@mixin make-column-push($columns) {
  left: percentage(($columns / $grid-columns));
}
@mixin make-column-pull($columns) {
  right: percentage(($columns / $grid-columns));
}



@mixin make-columns($i: 1, $list: ".l-col-#{$i}") {
  @for $i from (1 + 1) through $grid-columns {
    $list: "#{$list}, .l-col-#{$i}";
  }
  #{$list} {
    position: relative;
    min-height: 1px;
    padding-left:  ceil(($grid-gutter-width / 2));
    padding-right: floor(($grid-gutter-width / 2));
    box-sizing: border-box;
    flex: 1 0 100%;
    max-width: 100%;
  }
}


@mixin float-columns($i: 1, $list: ".l-col-#{$i}") {
  @for $i from (1 + 1) through $grid-columns {
    $list: "#{$list}, .l-col-#{$i}";
  }
  #{$list} {

  }
}


@mixin calc-column($index, $type) {
  @if ($type == width) and ($index > 0) {
    .l-col-#{$index} {
      flex: 0 0 percentage(($index / $grid-columns));
      max-width: percentage(($index / $grid-columns));
    }
  }
  @if ($type == push) and ($index > 0) {
    .l-col-push-#{$index} {
      left: percentage(($index / $grid-columns));
    }
  }
  @if ($type == push) and ($index == 0) {
    .l-col-push-0 {
      left: auto;
    }
  }
  @if ($type == pull) and ($index > 0) {
    .l-col-pull-#{$index} {
      right: percentage(($index / $grid-columns));
    }
  }
  @if ($type == pull) and ($index == 0) {
    .l-col-pull-0 {
      right: auto;
    }
  }
  @if ($type == offset) {
    .l-col-offset-#{$index} {
      margin-left: percentage(($index / $grid-columns));
    }
  }
}


@mixin loop-columns($columns, $type) {
  @for $i from 0 through $columns {
    @include calc-column($i, $type);
  }
}


// Create grid for col-$size
@mixin make-col-grid() {
  @include float-columns();
  @include loop-columns($grid-columns, width);
  @include loop-columns($grid-columns, pull);
  @include loop-columns($grid-columns, push);
  @include loop-columns($grid-columns, offset);
}




