.owl-nav{
  font-size: 0;
  direction: ltr;
  &.disabled{
    display: none;
  }
  .owl-prev, 
  .owl-next{
    cursor: pointer;
    display: inline-block;
    position: relative;
    vertical-align: top;
    font-size: 0;
    line-height: 0;
    background: none;
    box-shadow: none;
    border: none;
    padding: 0;
    width: 50px;
    height: 50px;
    background: #ffffff;
    border-radius: 50%;
    color: #242424;
    overflow: hidden;
    @include transition();

    .owl-nav__icon{
      position: absolute;
      display: block;
      left: 50%;
      top: 50%;
      font-size: 30px;
      line-height: 0;
      margin-left: -15px;
      margin-top: -15px;
    }

    &.disabled{
      opacity: 0.2;
      cursor: not-allowed;
    }
    &:hover{
      color: #ffffff;
      background: #1a9e98;
    }
  }
  .owl-prev{
    margin-right: 20px;
    &:after{
      border-left: 2px solid;
      border-bottom: 2px solid;
    }
  }
  .owl-next{
    &:after{
      border-top: 2px solid;
      border-right: 2px solid;
    }
  }
}
.owl-dots{
    text-align: center;
    line-height: 0;
    font-size: 0;
    position: relative;
    margin-top: 20px;
    direction: ltr;
    button.owl-dot{
      display: inline-block;
      vertical-align: top;
      position: relative;
      background: none;
      box-shadow: none;
      border: none;
      padding: 0;
      background: $color-black;
      width: 6px;
      height: 6px;
      border-radius: 50%;
      color: #ffffff;
      margin: 0 3px;
      font-size: 0;
      line-height: 0;
      @include transition();
      &.active{
        background: $color-red;
      }
    }
}
.owl-carousel{
    direction: ltr;
 &.l-row{
    margin: 0;
    display: block;
  }
  [class^=l-col] {
    width: auto;
    max-width: none;
    padding: 0;
    float: none;
    display: block;
  }
  .owl-nav{
    font-size: 0;
    .owl-prev{
      left: 0;
      @extend .owl-prev;
    }
    .owl-next{
      right: 0;
      @extend .owl-next;
    }
    .owl-prev, 
    .owl-next{
      position: absolute;
      display: block;
      top: 50%;
      margin: -25px 0 0 0;
    }
  }
}
.owl-item{
    > * {
      margin: 0;
      position: relative;
      z-index: 20;
    }
}
.owl-dots-inline{
  display: block;
  position: absolute;
  width: auto;
  height: 100%;
  bottom: 0;
  left: -2px;
  right: -2px;
  @include mediaMax($screen-xs){
    height: 7px;
  }
  ul{
    display: table;
    padding: 0;
    margin: 0;
    width: 100%;
    font-size: 0;
    line-height: 0;
    position: absolute;
    bottom: 0;
    height: 100%;
    li {
      margin: 0;
      padding: 0 2px;
      display: table-cell;
      position: relative;
      span {
        display: block;
        position: absolute;
        left: 2px;
        right: 2px;
        bottom: 4px;
        height: 3px;
        background: #FFFFFF;
        border-radius: 3px;
        &:after {
          content: '';
          display: block;
          position: absolute;
          background: #c07492;
          right: 0;
          width: 100%;
          height: 100%;
          max-width: 0;
          @include transition();
        }
      }
      &.is-prev {
        span:after {
          right: 0;
          left: auto;
        }
      }
      &.is-next {
        span:after {
          right: auto;
          left: 0;
        }
      }
      &.is-active {
        span:after {
          max-width: 100%;
        }
      }
    }
  }
}

.pswp__bg{
  background: rgba(#000,0.7);
}
.pswp__caption__center{
  text-align: center;
  max-width: none;
  line-height: 20px;
  padding: 10px;
  font-size: 16px;
}
.pswp__caption{
  min-height: 40px;
}

.datepickers-container{
  z-index: 1100;
}
.datepicker{
  z-index: 1100;
  direction: ltr;
  @include rtl(){
    left: auto;
    right: -100000000px;
    &.active{
      right: auto;
      left: 0;
    }
  }
}

.app-loader {
  display: block;
  position: fixed;
  background: rgba(#000,0.2) url("../images/loader.svg") center center no-repeat;
  background-size: 40px 40px;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 9999;
  font-size: 0;
  line-height: 0;
}